<template>
  <div>
    <span>{{msg}}</span>
    <button @click="fun"></button>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {mapActions, mapGetters} from 'vuex';

  export default {
    name: "index",
    methods: {
      ...mapActions([ // storeaction
        'fun', // src/store/actions.jsfun
      ]),
    },
    computed: {
      ...mapGetters([ // storegetters
        'msg' // src/store/getters.jsmsg
      ]),
    }
  }
</script>

<style scoped>

</style>



