export const fun = ({commit}) => {
  var params = new URLSearchParams();
  params.append('account', 'a123456');
  params.append('password', '123456');
  this.$http
    .post(`${this.$api}/login/account`, params)
    .then(msg => {
      commit({
        type: 'getMsg',
        msg,
      })
    })
}



