<template>
  <div id="sendPacket" class="page-wrap">
    <div class="page-head">
      <div class="toReturn" @click="toComponent('packetManage')">
        <img src="/static/img/left.png" alt="">
      </div>
      <div class="title"></div>
    </div>
    <div class="page-content">
      <div class="box-item">
        <label class="col-xs-4"></label>
        <div class="con-xs-8 text-right">
          <input type="text" class="text-right" placeholder="0.00">
          <span></span>
        </div>
      </div>
      <div class="box-item">
        <label class="col-xs-4"></label>
        <div class="con-xs-8 text-right">
          <input type="text" class="text-right" placeholder="">
          <span></span>
        </div>
      </div>
      <div class="box-item">
        <label class="col-xs-4"></label>
        <div class="con-xs-8 text-right">
          <div class="toLower">
            <label for="toLower"></label>
            <input type="radio" id="toLower" name="lower">
            <div class="user-defined active">
              <span class="circle active"></span>
            </div>
          </div>
          <div class="allLower">
            <label for="allLower"></label>
            <input type="radio" id="allLower" name="lower">
            <div class="user-defined">
              <span class="circle active"></span>
            </div>
          </div>
        </div>
      </div>
      <div class="box-item">
        <textarea name=""  rows="3" cols="10" placeholder=""></textarea>
      </div>
      <div class="box-amount"><span></span><span class="amount">4889</span></div>
      <div class="default-btn"></div>
      <p class="help-tips">1010</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "send-packet",
    data() {
      return {}
    },
    methods: {
      toComponent(component) {
        this.$root.Bus.$emit('toggleComponent', component)
      },
    },
  }
</script>

<style scoped>

</style>



