<template>
  <div id="my-content">
    <div class="assets">
      <div class="assets-title"> <span>()</span>
        <span class="close-ico" v-bind:class="{'open-ico':isShowAssets==1}"
              @click="toggle"></span>
      </div>
      <div class="assets-num" v-if="isShowAssets==1"><span class="amount">{{amount}}</span></div>
      <div class="assets-num" v-else>******</div>
      <div class="withdrawal-btn" @click="toComponent('withdrawal')"></div>
      <div class="recharge-btn" @click="toComponent('recharge')"></div>
    </div>
    <div class="my-detail">
      <div class="detail-line">
        <div class="item">
          <div @click="toComponent('reportRecord')">
            <img src="/static/img/Edition.png" alt="">
            <div class="item-info">
              <div class="title"></div>
              <div class="des"></div>
            </div>
          </div>
        </div>
        <div class="item last-item">
          <div @click="toComponent('record')">
            <img src="/static/img/IP.png" alt="">
            <div class="item-info">
              <div class="title"></div>
              <div class="des"></div>
            </div>
          </div>
        </div>
        <div class="line"></div>
      </div>
      <div class="detail-line">
        <div class="item">
          <div @click="toComponent('capitalRecord')">
            <img src="/static/img/Record.png" alt="">
            <div class="item-info">
              <div class="title"></div>
              <div class="des"></div>
            </div>
          </div>
        </div>
        <!--<div class="item last-item">-->
        <!--<img src="/static/img/share.png" alt="">-->
        <!--<div class="item-info">-->
        <!--<div class="title"></div>-->
        <!--<div class="des">0.05</div>-->
        <!--</div>-->
        <!--</div>-->
        <div class="line"></div>
      </div>
    </div>

    <div class="agent-manege" v-if="user_type==2">
      <div class="detail-line">
        <div class="item">
          <img src="/static/img/Statistics.png" alt="">
          <div class="item-info">
            <div class="title"></div>
            <div class="des"></div>
          </div>
        </div>
        <div class="item last-item" @click="toComponent('lowerManage')">
          <img src="/static/img/Management.png" alt="">
          <div class="item-info">
            <div class="title"></div>
            <div class="des"></div>
          </div>
        </div>
        <div class="line"></div>
      </div>
      <div class="detail-line">
        <div class="item" @click="toComponent('openAccount')">
          <img src="/static/img/accounts.png" alt="">
          <div class="item-info">
            <div class="title"></div>
            <div class="des"></div>
          </div>
        </div>
        <div class="item last-item" @click="toComponent('packetManage')">
          <img src="/static/img/Bag.png" alt="">
          <div class="item-info">
            <div class="title"></div>
            <div class="des"></div>
          </div>
        </div>
        <div class="line"></div>
      </div>
      <div class="detail-line">
        <div class="item" @click="toComponent('shareBonus')">
          <img src="/static/img/Record.png" alt="">
          <div class="item-info">
            <div class="title"></div>
            <div class="des"></div>
          </div>
        </div>
        <div class="item last-item" @click="toComponent('dailyWages')">
          <img src="/static/img/Wages.png" alt="">
          <div class="item-info">
            <div class="title"></div>
            <div class="des"></div>
          </div>
        </div>
        <div class="line"></div>
      </div>
    </div>

    <div class="my-games">
      <div class="title">
        <img src="/static/img/line.png" alt="">
        <div class="my-play"></div>
      </div>
      <div class="online-games">
        <swiper :options="swiperOption">
          <swiper-slide>
            <img src="/static/img/ico03.png" alt="">
            <div class="game-name"></div>
          </swiper-slide>
          <swiper-slide>
            <img src="/static/img/ico05.png" alt="">
            <div class="game-name"></div>
          </swiper-slide>
          <swiper-slide>
            <img src="/static/img/ico08.png" alt="">
            <div class="game-name"></div>
          </swiper-slide>
          <swiper-slide>
            <img src="/static/img/ico09.png" alt="">
            <div class="game-name"></div>
          </swiper-slide>
          <swiper-slide>
            <img src="/static/img/ico09.png" alt="323">
            <div class="game-name">432143</div>
          </swiper-slide>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
  import {setLocalStorage, getLocalStorage} from "../../../static/js/util";

  export default {
    name: "my-content",
    props: ['user_type'],
    created() {
      this.getAssets();
      this.getState();
    },
    data: function () {
      return {
        amount: '',
        isShowAssets: 0,
        swiperOption: {
          slidesPerView: 4,
          spaceBetween: 10
        }
      }
    },
    methods: {
      getState: function () {
        this.isShowAssets = getLocalStorage("showAssets");
      },
      getAssets: function () {
        var user_id = getLocalStorage("user_id");
        var username = getLocalStorage("username");
        var session = getLocalStorage("session");
        this.$http
          .post(`${this.$api}/v1/wallet/r/get_user_balance/${user_id}/${username}?session=${session}`)
          .then(res => {
            var resData = res.data;
            if (resData.success == true) {
              this.amount = resData.balance;
            } else {

            }
          })
      },
      toggle: function () {
        var showstate = getLocalStorage("showAssets");
        if (showstate != 1) {
          this.isShowAssets = 1;
          setLocalStorage("showAssets", this.isShowAssets);
        } else {
          this.isShowAssets = 0;
          setLocalStorage("showAssets", this.isShowAssets);
        }
      },
      toComponent(component) {
        this.$root.Bus.$emit('toggleComponent', component);
        if (component == 'recharge') {
          this.$root.Bus.$emit('footerStatus', 'recharge')
        }
      },
    }
  }
</script>

<style scoped lang="less">


</style>



